body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

html body {
  background: #fff;
}
.container {
    display: flex;
    min-height: 80%;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 20px;
    padding-bottom: 50px;
    width: 90%;
    margin: 0 auto;
    z-index: -1;
  }
  
  @media (min-width: 1100px) {
    .container {
      padding: 9em 0;
      max-width: 68rem;
    }
  }
.gameCard {
    padding:5em;
    margin-right: 20px;
    margin-bottom: 20px;
    background-size: cover;
}

@media (max-width: 750px) {

    .gameCard {
        height: 100px;
        width: 100px;
    }
}

@media (max-width: 400px) {

    .gameCard {
        height: 75px;
        width: 75px;
    }
}
.header {
    background: rgb(22, 5, 83);
    width: 100%;
    min-height: 80px;
    color: ivory;
    position: fixed;
    top: 0;
    margin: 0 auto;
    z-index: 1000;
}

.header ul {
    list-style-type: none;
    margin: 0 auto;
    padding: 0;
    width: 100%;
    height: 100%;
    text-align: center;
}

.header li {
    display: inline-block;
    width: 33%;
    padding-top: 20px;
    font-family: 'Chakra Petch', sans-serif;
}

#score {
    font-size: 1.2em;
}


@media (max-width: 700px) {

    .header li {
        display: block;
        text-align: center;
        width: 100%;
        padding-top: 0;
    } 
}

.gameMessage {
    font-size: 1.5em;
    font-family: 'Ubuntu', sans-serif;
}

.black {
    color: ivory;
}

.correct {
    color: LAWNGREEN;
}

.incorrect {
    color: darkred;
    -webkit-text-stroke: 1px black;
}
